import React from 'react';
import { graphql } from 'gatsby';
import { GatsbySeo } from 'gatsby-plugin-next-seo';

import Layoout from '../components/Layout';
import Container from '../components/Container';
import Banner from '../components/Banner';
import BlockHeaderNoImage from '../components/BlockHeaderNoImage';
import BlockLogos from '../components/BlockLogos';
import BlockImageRight from '../components/BlockImageRight';
import BlockCards from '../components/BlockCards';
import Block3PartIllo from '../components/Block3PartIllo';
import Block4Part from '../components/Block4Part';
import Block4PartCompanies from '../components/Block4PartCompanies';
import Footer from '../components/Footer';
import SpaceDivider from '../components/SpaceDivider';

import TimeIcon from '../components/icons/Time';
import ShieldIcon from '../components/icons/Shield';
import DropIcon from '../components/icons/Drop';

import { edgesByName } from '../utils/gql';

export const pageQuery = graphql`
  {
    images: allFile(filter: { relativeDirectory: { eq: "advance-pay" } }) {
      edges {
        node {
          name
          childImageSharp {
            fluid {
              base64
              tracedSVG
              srcWebp
              srcSetWebp
              originalImg
              originalName
            }
          }
        }
      }
    }
    banner: file(relativePath: { eq: "banners/why-sell-banner.png" }) {
      childImageSharp {
        fluid {
          base64
          tracedSVG
          srcWebp
          srcSetWebp
          originalImg
          originalName
        }
      }
    }
    companies: allFile(filter: { relativeDirectory: { eq: "companies" } }) {
      edges {
        node {
          name
          childImageSharp {
            fluid {
              base64
              tracedSVG
              srcWebp
              srcSetWebp
              originalImg
              originalName
            }
          }
        }
      }
    }
  }
`;

